
.wrapper {
  font-family: var(--sb-font-family);
  color: var(--sb-primary-color);
  background-color: var(--sb-default-background);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.box {
  width: 950px; 
  height: 530px;
  display: flex;
  box-shadow: 5px 5px 50px  var(--blue-shadow);
  border-radius: 10px;
  position: relative;
}

.welcome-card {
  background-color: white;
  flex: 4;
  position: relative;
  border-top: var(--sb-accent-color) 5px solid;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.welcome-card strong {
  color: var(--sb-accent-color);
  font-weight: 700;
}

.welcome-text {
  margin-left: 30px;     
  z-index: 2;   
  font-family: 'Poppins';
  line-height: 10px;
  font-weight: 700;
}

.small-text{
  font-size: 25px;
}
.large-text{
  font-size: 39px;
}
.welcome-text-2 {
  margin-left: 25px;
  font-size: 13px;
  font-weight: 600;
  position: relative;
}

.welcome-text-2 p{
  display: inline;
  margin-left: 29px;
}

.welcome-text-2 img{
  position: absolute;
  top: -3px;
  display: inline;
  height: 20px;
  width: auto;
}

.logo img {
  margin: 10px 20px;
  height: 50px;
  width: auto;
}
.spaceship-image {
  position: relative;
  position: absolute;
  bottom: -229px;
  left: -142px;
}

.spaceship-image img {
  height: 600px;
  width: auto;
  user-select: none; 
}

.download-instructions {
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  position: relative;
  flex: 5;
  background-color: var(--gray-white-opensite);
  background-image: url('https://storage.googleapis.com/sessionbox-content/GRADIENT_685dbe4d5e/GRADIENT_685dbe4d5e.png?updated_at=2023-07-24T10:40:14.323Z');
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.triangle {
  position: absolute;
  top: 150px;
  left: -1px;
}

.arrow img {
  height: 350px;
  width: auto;
  position: absolute;
  bottom: -155px;
  right: -150px;
  z-index: 4;
}

.download-instructions .text {
  font-size: 13px;
  font-weight: 500;
  margin-bottom: 40px;
  padding-bottom: 20px;
}

.line {
  background-color: var(--sb-accent-color);
  height: 280px;
  width: 2px;
  position: absolute;
  top: 52%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.download-button {
  margin-top: 25px;
  font-size: 14px;
  font-weight: 600;
}

.step-done {
  border: var(--sb-accent-color) 1px solid;
  margin-bottom: 40px;
}

.step-done::before {
  content: "";
  position: absolute;
  top: -6px;
  left: 50%;
  transform: translate(-50%, 0);
  width: 10px;
  height: 5px;
  background-color: var(--gray-white-opensite);
  z-index: 0; 
}

.step-done::after {
  content: "";
  position: absolute;
  bottom: -6px;
  left: 50%;
  transform: translate(-50%, 0);
  width: 10px;
  height: 5px;
  background-color: var(--gray-white-opensite);
  z-index: 0; 
}

.step-todo:before {
  content: "";
  position: absolute;
  top: -5px;
  left: 50%;
  transform: translate(-50%, 0);
  width: 10px;
  height: 5px;
  background-color: var(--gray-white-opensite);
  z-index: 0; 
}

.step-todo::after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 50%;
  transform: translate(-50%, 0);
  width: 10px;
  height: 5px;
  background-color: var(--gray-white-opensite); 
  z-index: 0; 
}

.steps {
  margin-right: 5px;
  margin-top: 5px;
  margin-left: 5px;
  z-index: 2;
  display: flex;
  align-items: center;
  gap: 20px;
  border-radius: 10px;
  background: white;
  padding: 7px 40px 10px 20px;
  position: relative;
  z-index: 3;
  min-width: 210px;
}

.step-1, .step-2 {
  position: absolute;
  top: -22px;
  left: 5px;
  font-size: 12px;
}

.step-1 {
  color: var(--sb-accent-color);
}

.step-2 {
  color: var(--sb-light-font-color);
}

.shadow {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 10px  var(--blue-shadow); 
  pointer-events: none;
  z-index: 1; 
  border-radius: 10px;
}

.green-text {
  color: var(--sb-accent-color);
}

.steps img {
  height: 45px;
  width: auto;
}

.sbo {
  font-family: var(--sb-brand-font-family);
  font-size: 20px;
  line-height: 0;
  font-weight: bolder;
}

.green-text {
  line-height: 9px;
  font-size: 12px;
}

.download-instructions .text {
  font-size: 13px;
  font-weight: 500;
  margin-bottom: 40px;
}